:root {
  --pink-color: #ffbff7;
  --yellow-color: #fcd418;
  --blue-color: #2e3192;
}

#root {
  overflow-x: hidden;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'pier-sans_regular', 'Arial', sans-serif;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f8f8f8;
}

button {
  padding: unset;
  color: unset;
  border: unset;
  background-color: unset;
  font: unset;
}

.clickable {
  cursor: pointer;
}

.non-sel {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
footer {
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	background-color: var(--yellow-color);
}

.footer-logos {
	margin: 25px;
	max-width: 500px;
	width: 100%;
	height: 40px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	-webkit-align-items: center;
	        align-items: center;
}

.footer-logo {
	height: 100%;
}

.footer-logo .injected-svg {
	height: 40px;
	width: auto;
	width: initial;
}

.footer-logo svg {
	fill: white;
}

.footer-text {
	color: white;
	padding: 5px 0 40px;
	text-align: center;
}
.navbar {
	width: 100%;
	height: 65px;
}

.navbar-content {
	z-index: 1;
	position: fixed;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
	min-height: 65px;
	background-color: var(--pink-color);
}

.navbar-delimiter {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	width: 1000px;
	height: 100%;
	font-size: 20pt;
	color: white;
}

.navlink {
	-webkit-flex: 0 1 17%;
	        flex: 0 1 17%; /*flex: 0 1 20%;*/
    display: none;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	min-height: 65px;
	color: white;
	text-decoration: none;
}

.navimg {
	-webkit-flex: 1 1 30%;
	        flex: 1 1 30%;
    display: -webkit-flex;
    display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
    -webkit-align-items: center;
            align-items: center;
	min-height: 65px;
}

.navimg img {
    max-height: 38px;
    max-width: 165px;
}

.navtop {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex: 0 1 50%;
	        flex: 0 1 50%;
}

.navtop-side {
	-webkit-flex: 0 0 10%;
	        flex: 0 0 10%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	margin: 10px;
}

.navmenu-open {
	display: -webkit-flex;
	display: flex;
}

.active {
	text-decoration: underline;
}

@media only screen and (min-width: 600px) {
	/* For tablets and bigger */
	.navbar-delimiter {
		-webkit-flex-direction: unset;
		        flex-direction: unset;
	}

	.navtop-side {
		display: none;
	}

	.navlink {
		display: -webkit-flex;
		display: flex;
	}
}
.background {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	height: 350px;
	background-repeat: repeat-y;
	background-attachment: fixed;
	background-size: cover;
}

.background img {
	background-color: white;
	border-radius: 50%;
	max-height: 200px;
}
.page-background {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	text-align: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	width: 100%;
	height: 500px;
	color: white;
}

.page-background p {
	max-width: 80%;
	margin: 10px;
	font-size: 14pt;
}

.page-background h1 {
	margin: 10px;
	font-size: 30pt;
}

.page-image {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	margin-top: 0;
}

.page-image img {
	width: 150%;
}

@media only screen and (min-width: 600px) {
	/* For tablets and bigger */
	.page-background {
		-webkit-justify-content: unset;
		        justify-content: unset;
		padding-top: 20px;
		height: 200px;
	}
	.page-background p {
		max-width: 60%;
	}
	.page-image {
		margin-top: -4%;
	}
	.page-image img {
		width: 80%;
	}
}
.mission {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	width: 100%;
	background-color: white;
}

.mission-child {
	-webkit-flex: 1 1;
	        flex: 1 1;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	margin: 10% 15%;
	min-height: 200px;
}

.mission-text {
	font-size: 25px;
}

.mission-button-child {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex: 1 1;
	        flex: 1 1;
}

.mission-button-text {
	color: var(--blue-color);
	font-size: 30pt;
	font-weight: bold;
	text-align: center;
}

.mission-button {
	border-radius: 8px;
	font-size: 20px;
	background-color: lightseagreen;
	color: white;
	border: none;
	padding: 10px 40px;
	text-align: center;
}

@media only screen and (min-width: 600px) {
	/* For tablets and bigger */
	.mission {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}

	.mission-child {
		min-height: 300px;
		margin: 5% 5%;
	}
}
.banner {
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	display: -webkit-flex;
	display: flex;
	overflow: hidden;
}

.big-banner img {
	width: 100%;
}

.big-banner {
	width: 100%;
}

.small-banner {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 0;
	        flex: 1 0;
}

.small-banner-content {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	padding: 25px;
}

.small-banner-img {
	display: -webkit-flex;
	display: flex;
	height: 200px;
	padding: 20px;
	-webkit-flex: 0 0;
	        flex: 0 0;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	max-width: 100%;
}

.small-banner-img img {
	max-height: 95%;
	max-width: 100%;
}

.small-banner-text {
	-webkit-flex: 0 0;
	        flex: 0 0;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	text-align: center;
	font-size: 23pt;
	font-weight: bold;
}

.small-banner-button {
	display: -webkit-flex;
	display: flex;
	border: 2px solid;
	display: flex;
	padding: 5px;
	-webkit-justify-content: center;
	        justify-content: center;
	text-decoration: none;
	-webkit-align-items: center;
	        align-items: center;
	border-radius: 7%;
	width: 50%;
}

.small-banners {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

@media only screen and (min-width: 600px) {
	/* For tablets and desktop */
	.small-banners {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
}
.event-type-info {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	height: 600px;
	width: 100%;
}

@media only screen and (min-width: 600px) {
	/* For tablets and bigger */
	.event-type-info {
		height: 400px;
	}
	.left {
		-webkit-flex-direction: row-reverse;
		        flex-direction: row-reverse;
	}
	.right {
		-webkit-flex-direction: row;
		        flex-direction: row;
	}
}

.event-type-info>div {
	-webkit-flex: 1 0;
	        flex: 1 0;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.event-type-info-img {
	-webkit-flex-direction: row;
	        flex-direction: row;
	overflow: hidden;
	max-width: 100%;
	max-height: 100%;
}

.event-type-info-img img {
	height: 100%;
	object-fit: cover;
}

.event-type-info-text {
	-webkit-flex-direction: column;
	        flex-direction: column;
}
hr {
	margin: 30px 25%;
}
.event-gallery {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-flow: row wrap;
	        flex-flow: row wrap;
	list-style-type: none;
	margin: 0;
	padding: 20px 0;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
}

.event-gallery-item {
	min-width: 250px;
	-webkit-flex: 0 1 20%;
	        flex: 0 1 20%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 20px 5px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	background-color: white;
}

.event-gallery-item-img {
	height: 100%;
	padding: 20px;
}

.event-gallery-item-img img {
	height: 120px;
	width: 100%;
	object-fit: cover;
}

.event-gallery-item-text {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	padding: 20px;
	padding-top: 0;
}

.event-gallery-item-text p, .event-gallery-item-text h3, .event-gallery-item-text h4 {
	margin: 0;
}

.event-gallery-item-text p {
	margin-top: 10px;
}

.event-gallery-item-launch {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.event-gallery-item-launch svg {
	fill: grey;
	margin: 2px;
}

.toggleGallery {
	width: 100%;
}

.toggleGallery svg {
	width: 100%;
}
